@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:200,200italic,300,300italic,regular,italic,600,600italic,700,700italic,800,800italic,900,900italic);

$primary-color: #feaa2b;
$secondary-color: #ff5757;
$light-color: #ffea54;
$smooth-color: #ffcb3c;
$bg-color: #102542;
$pirmary-font: "Poppins", "Nunito Sans";
$secondary-font: "Nunito Sans";
body {
  font-family: $pirmary-font;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
