@import "./Utama.scss";

.container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  &::before {
    content: "";
    background: $secondary-color;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    animation: slidegg 1.5s;
  }
  .text {
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    text-align: center;
    color: white;
    h1 {
      letter-spacing: 1px;
    }
    .ketentuan {
      color: #e2e2e2;
      font-size: 12px;
    }
    .klikme {
      margin-top: 20px;
      background: $primary-color;
      color: white;
      font-family: "Poppins", "Nunito Sans";
      font-weight: bolder;
      letter-spacing: 2px;
      transition: all 0.3s;
      &:hover {
        color: $bg-color;
      }
    }
  }
  .img {
    width: 60%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    img {
      animation: gganim 1.5s;
      width: 80%;
    }
  }
  .layer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 25px 30px;
    background: $bg-color;
    width: 30%;
    height: 40%;
    display: flex;
    transform: translate(300%, -50%);
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: 2px solid $primary-color;
    transition: all 0.4s;
    .text {
      color: $primary-color;
      width: 100%;
      @media screen and (min-width: 300px) {
        justify-content: center;
      }
    }
    .closeicons {
      cursor: pointer;
      position: absolute;
      right: 5%;
      top: 7%;
      font-size: 30px;
      font-weight: 500;
      color: $primary-color;
    }
  }
  @media screen and (max-width: 600px) {
    flex-direction: column;
    .text {
      justify-content: flex-end;
    }
    .text,
    .img {
      width: 100%;
    }
    .img {
      justify-content: center;
    }
    .layer {
      width: 80%;
      height: 30%;
      padding: 10px;
      .text {
        font-size: 18px;
      }
    }
  }
}

.slidenotif {
  transform: translate(-50%, -50%) !important;
}

@keyframes gganim {
  0% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  100% {
    clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  }
}
@keyframes slidegg {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
