@import "./Utama.scss";

.about {
  z-index: -1;
  height: 90vh;
  &::before {
    content: "";
    z-index: -99;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: $smooth-color;
    animation: slideBg 1.5s;
  }
}
.text-about {
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h1 {
    font-size: 4rem;
    letter-spacing: 1.4px;
    text-transform: uppercase;
  }
  h5 {
    font-size: 1rem;
  }
  h1,
  h5 {
    animation: displayn 2s;
  }

  span {
    color: $bg-color;
  }
  @media screen and (max-width: 600px) {
    h1 {
      font-size: 1.7rem;
      white-space: nowrap;
    }
    h5 {
      font-size: 0.7rem;
    }
  }
}
.sosial {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  .sosial-icon {
    display: flex;
    width: 60px;
    height: 60px;
    border-radius: 19px;
    background: #ffcb3c;
    box-shadow: 13px -13px 26px #bd962c, -13px 13px 26px #ffff4c;
    margin: 0 10px;
    cursor: pointer;
    transition: all 0.3s;
    &:focus,
    &:hover,
    &:focus-within {
      transform: scale(0.9);
      border-radius: 44px;
      background: #ffcb3c;
      box-shadow: -7px 7px 12px #deb134, 7px -7px 12px #ffe544;
    }
  }
  .icon {
    margin: auto;
    font-size: 30px;
  }
  @media screen and (max-width: 600px) {
    flex-direction: row;
  }
}
