@keyframes slideBg {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
@keyframes displayn {
  0% {
    opacity: 0;
    z-index: -1;
  }
  100% {
    opacity: 1;
    z-index: -1;
  }
}
