@import "./Utama.scss";
.project-container {
  padding-top: 110px;
  background: $light-color;
  .judul-project {
    margin-bottom: 20px;
    font-size: 2.3rem;
    text-align: center;
  }
}
.project {
  width: 100%;
  display: grid;
  grid-row-gap: 100px;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  background: $light-color;
  @media screen and (max-width: 600px) {
    grid-template-rows: 1fr;
    grid-template-columns: none;
    grid-row-gap: 25px;
  }
}
