@import "./Utama.scss";
.root-card {
  margin: auto;
}
.card {
  margin: auto;
  width: 90%;
  border-radius: 5px;
  background: white;
  position: relative;
  overflow: hidden;
  box-shadow: 2px 10px 13px 0px rgba(49, 49, 49, 0.48);
  -webkit-box-shadow: 2px 10px 13px 0px rgba(49, 49, 49, 0.48);
  -moz-box-shadow: 2px 10px 13px 0px rgba(49, 49, 49, 0.48);
  .img {
    width: 100%;
    height: 60%;
    img {
      width: 100%;
      height: 100%;
      vertical-align: middle;
    }
  }
  .text {
    padding: 30px 20px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    background: $secondary-color;
    h3 {
      font-size: 15px;
    }
    .btn-project {
      font-weight: bold;
      background-color: $primary-color;
      font-family: $pirmary-font;
    }
  }
}
