@import "./Utama.scss";

.navbar {
  display: flex;
  font-family: $pirmary-font;
  width: 100vw;
  justify-content: space-between;
  align-items: center;
  padding: 20px 15px;
  height: 10vh;
  box-sizing: border-box;
  background: $bg-color;
  position: fixed;
  width: 100vw;
  z-index: 99;
  .logo {
    h1 {
      font-size: 1.6rem;
      color: $primary-color;
      cursor: pointer;
      transition: all 0.3s;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      &:hover {
        color: $smooth-color;
      }
    }
  }
  .menu-bar {
    display: none;
  }
  .close {
    display: none;
  }
  ul.link-items {
    display: flex;
    transition: all 0.5s;
    z-index: 99;
    li {
      padding: 10px 15px;
    }
    a {
      color: $secondary-color;
      transition: all 0.3s;
      font-weight: 500;
      &:hover {
        color: $smooth-color;
      }
    }
  }
  @media screen and (max-width: 600px) {
    align-items: center;
    .logo {
      h1 {
        font-size: 1em;
      }
    }
    .link-items {
      position: absolute;
      flex-direction: column;
      height: 100vh;
      top: 0;
      left: 0;
      background: $bg-color;
      justify-content: center;
      width: 250px;
      transform: translateX(-100%);
      overflow-x: hidden;
      li {
        text-align: center;
      }
    }
    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      display: block;
      svg {
        font-size: 35px;
        fill: red;
        font-weight: 600;
        &:hover {
          fill: rgb(248, 82, 82);
        }
      }
    }
    .menu-bar {
      cursor: pointer;
      display: block;
      svg {
        fill: $primary-color;
        vertical-align: middle;
      }
    }
  }
}

.slidebar {
  transform: translateX(0%) !important;
}
